
html, body, .container-table {
  height: 100%;
  background-color: #575778 !important;
}
.container-table {
    display: table;
    color: white;
}
.vertical-center-row {
    display: table-cell;
    vertical-align: middle;
}
.vertical-20p {
  padding-top: 20%;
}
.vertical-5p {
  padding-top: 5%;
}
.logo {
  width: 15em;
  padding: 3em;
}
.loginForm .input-group {
  padding-bottom: 1em;
  height: 4em;
}
.loginPage input.form-control {
  height: 2em;
  background-color:transparent;
  border:none;
  border-bottom:1px solid #fff;
}
button.w100 {
    width: 100%;
}
.loginPage .nav>li>a {
  color:#8989A0;
}
.loginPage .nav-tabs>li.active>a,.loginPage .nav-tabs>li.active>a:focus,.loginPage .nav-tabs>li.active>a:hover {
    color: #fff;
    cursor: default;
  border:none;
    border-bottom: 1px solid #fff;
        background-color: transparent;
}
.loginPage .nav > li > a:hover,.loginPage .nav > li > a:active,.loginPage .nav > li > a:focus {
    color: #f7f7f7;
     background:none;
     border:none;
border-bottom: 1px solid #f7f7f7;
}




