
.user-panel {
  height: 85px;
}
.user-panel .image {
  border-radius: 50%;
  overflow: hidden;
  width: 45px;
  height: 45px;
}
.user-panel .image img {
  width: 100%;
  height: 100%;
}
.sidebar-collapse .user-panel .image {
  border-radius: 50%;
  overflow: hidden;
  width: 2.1em;
  height: 2.1em;
}
.user-menu a {
  cursor: pointer;
}
hr.visible-xs-block {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.17);
  height: 1px;
  border-color: transparent;
}
.input-group-addon {
  min-width: 50px;
}
.main-sidebar .user-panel,
.sidebar-menu,
.sidebar-menu>li.header {
  white-space: normal;
}
.navbar-title {
  font-size: 21px;
  color: #fff;
  padding-top: 10px;
  display: inline-block;
  text-transform: uppercase;
  font-weight: bolder;
  padding-left: 15px;
}



